.seadbar-up{
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.4);
  display: none;
}
.menu{
  width: 300px;
  height: 100vh;
  padding-top: 46px;
  background-color: #fff;
  position: fixed;
  left: -300px;
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.menu>div{
  width: 215px;
  margin: 0 auto;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.user-info{
  text-align: center;
  color: #F77062;
}
.link-img{
  width: 100px;
  height: 100px;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #ccc;
}
.menu .link-list{
  width: 215px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 5;
      flex-grow: 5;
}
.link-list a{
  line-height: 57px;
  text-decoration: none;
  border-bottom: 1px solid #ccc;
  color: #999;
}
.menu .button{
  text-align: center;
  -ms-flex-positive: 3;
      flex-grow: 3;
}
.button button{
  width: 172px;
  line-height: 41px;
  color : #fff;
  background-image: -webkit-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: -o-linear-gradient(135deg, #F77062 0%, #FE5196 100%);
  background-image: linear-gradient(-45deg, #F77062 0%, #FE5196 100%);
  border: none;
  border-radius: 4px;
}



